'use client';
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'react-hot-toast';
import { supabase } from '@/lib/SupabaseClient';

export default function CreateAuctionPage() {
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState({
    itemName: '',
    description: '',
    startingPrice: '',
    bidIncrement: '',
    duration: '1', // in hours
    imageUrl: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);

    // Calculate start and end times
    const now = new Date();
    const startTime = now.toISOString();
    const endTime = new Date(now.getTime() + formData.duration * 60 * 60 * 1000).toISOString();

    // In a real app, you'd get the seller_id from an auth session
    const MOCK_SELLER_ID = '634b0123-20ac-4a59-8bd2-6f2cb893c34b'; // Replace with a real user UUID from your Supabase auth table

    const { error } = await supabase.from('auctions').insert({
      item_name: formData.itemName,
      description: formData.description,
      starting_price: parseFloat(formData.startingPrice),
      bid_increment: parseFloat(formData.bidIncrement),
      image_url: formData.imageUrl,
      start_time: startTime,
      end_time: endTime,
      seller_id: MOCK_SELLER_ID,
      status: 'active',
    });

    setIsLoading(false);

    if (error) {
      console.error("Error creating auction:", error);
      toast.error(error.message);
    } else {
      toast.success("Auction created successfully!");
      router.push('/');
    }
  };

  return (
    <div className="max-w-2xl mx-auto bg-white p-8 rounded-lg shadow-lg">
      <h1 className="text-3xl font-bold mb-6 text-center">Create a New Auction</h1>
      <form onSubmit={handleSubmit} className="space-y-6">
        <div>
          <label htmlFor="itemName" className="block text-sm font-medium text-gray-700">Item Name</label>
          <input required type="text" name="itemName" id="itemName" value={formData.itemName} onChange={handleChange} className="mt-1 block w-full p-3 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500" />
        </div>
        <div>
          <label htmlFor="description" className="block text-sm font-medium text-gray-700">Description</label>
          <textarea required name="description" id="description" rows="4" value={formData.description} onChange={handleChange} className="mt-1 block w-full p-3 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500"></textarea>
        </div>
         <div>
          <label htmlFor="imageUrl" className="block text-sm font-medium text-gray-700">Image URL</label>
          <input type="url" name="imageUrl" id="imageUrl" value={formData.imageUrl} onChange={handleChange} placeholder="https://example.com/image.png" className="mt-1 block w-full p-3 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500" />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label htmlFor="startingPrice" className="block text-sm font-medium text-gray-700">Starting Price ($)</label>
            <input required type="number" name="startingPrice" id="startingPrice" value={formData.startingPrice} onChange={handleChange} className="mt-1 block w-full p-3 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500" />
          </div>
          <div>
            <label htmlFor="bidIncrement" className="block text-sm font-medium text-gray-700">Bid Increment ($)</label>
            <input required type="number" name="bidIncrement" id="bidIncrement" value={formData.bidIncrement} onChange={handleChange} className="mt-1 block w-full p-3 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500" />
          </div>
        </div>
        <div>
          <label htmlFor="duration" className="block text-sm font-medium text-gray-700">Auction Duration</label>
          <select name="duration" id="duration" value={formData.duration} onChange={handleChange} className="mt-1 block w-full p-3 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500">
            <option value="1">1 Hour</option>
            <option value="6">6 Hours</option>
            <option value="12">12 Hours</option>
            <option value="24">1 Day</option>
            <option value="72">3 Days</option>
          </select>
        </div>
        <div>
          <button type="submit" disabled={isLoading} className="w-full bg-blue-600 text-white font-bold py-3 px-4 rounded-md hover:bg-blue-700 transition-colors shadow-lg disabled:bg-gray-400">
            {isLoading ? 'Creating...' : 'List My Item'}
          </button>
        </div>
      </form>
    </div>
  );
}
